import tabbar from './tabbar'
import { token } from '@/tools'
export default {
  components: {
    tabbar
  },
  data() {
    return {
      menuList: [
        {
          id: 'home',
          iconPath: 'images/icon/home.png',
          selectIconPath: 'images/icon/home-selected.png',
          path: { name: 'home' }
        },
        {
          id: 'search',
          iconPath: 'images/icon/community.png',
          selectIconPath: 'images/icon/community-selected.png',
          path: { name: 'search' }
        },
        {
          id: 'push',
          iconPath: 'images/icon/add.png',
          selectIconPath: 'images/icon/add.png',
          path: { name: 'createproject' }
        },
        {
          id: 'notice',
          iconPath: 'images/icon/notice.png',
          selectIconPath: 'images/icon/notice-selected.png',
          path: { name: 'notice' }
        },
        {
          id: 'user',
          iconPath: 'images/icon/me.png',
          selectIconPath: 'images/icon/me-selected.png',
          path: { name: 'user' }
        }
      ]
    }
  },

  methods: {
    onPushChild() {
      const tokenStr = token.getToken()
      if (!tokenStr) {
        this.$router.push({ name: 'sign' })
      } else {
        this.$router.push({ name: 'home' })
      }
    }
  },
  mounted() {
    this.onPushChild()
  },
  route: {}
}
