export default {
  model: { prop: 'value', event: 'change' },
  props: {
    value: {
      type: [String, Number, Object],
      default: ''
    },
    tabs: Array
  },
  data() {
    return {
      currentValue: ''
    }
  },

  methods: {
    sendData(value) {
      console.log(value + '---' + this.tabs[0].id)

      this.currentValue = value
      console.log('改变', this.currentValue)
    }
  },
  mounted() {
    this.currentValue = this.tabs[0].id
  },
  watch: {
    tabs: {
      handler: function() {
        console.log('触发事件', this.tabs)
        this.currentValue = this.value ? this.value : this.tabs[0].value
      },
      immediate: true
    },
    currentValue(value) {
      console.log('触发方法')
      this.$emit('change', value)
    }
  }
}
